.divider {
    width: 10px;
    height: 100%;
    border-radius: 2px;
    background-color: rgba(255, 255, 255, 0.1);
  }
  
  .divider__container {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 4px;
  }
  
  .divider__container:hover {
    cursor: ns-resize;
  }
  